import styles from './timeline.module.scss'
import { useState } from 'react'
import classNames from 'classnames'
import { TimelineEventModel } from './timeline'

interface TimelineEventProps {
    readonly event: TimelineEventModel
}

function TimelineEvent({ event }: TimelineEventProps) {
    const [contentCollapsed, setContentCollapsed] = useState(true)

    return (
        <div className={styles.TimelineEvent} key={event.title}>
            <div className={styles.TimelineEventSide}>
                <div className={styles.TimelineEventSideLine}>
                    <div className={styles.TimelineEventSideLinePoint}>
                        <div className={styles.TimelineEventSideLinePointTimestamp}>
                            {event.timestamp}
                        </div>
                        <div className={styles.TimelineEventSideLinePointCircle} />
                    </div>
                </div>
            </div>
            <div
                className={styles.TimelineEventTitle}
                onClick={() => setContentCollapsed(cc => !cc)}
            >
                {event.title}
            </div>
            {contentCollapsed ? (
                <div
                    className={styles.TimelineEventContentFade}
                    onClick={() => setContentCollapsed(false)}
                />
            ) : (
                <div />
            )}
            <div
                className={classNames(styles.TimelineEventContent, {
                    [styles.TimelineEventContentCollapsed]: contentCollapsed,
                })}
            >
                {event.content}
            </div>
            <div className={styles.TimelineEventTags}>
                {event.tags
                    ?.sort((first, second) => first.localeCompare(second))
                    ?.map(tag => (
                        <span className={styles.TimelineEventTagsTag} key={tag}>
                            {tag}
                        </span>
                    ))}
            </div>
        </div>
    )
}

export default TimelineEvent
